import React from "react";
import mobxStore from "./../../mobx-store/mobex-store";
import { Segment, Tab } from "semantic-ui-react";
import { observer } from "mobx-react";
import TaskList from "../task-list";
import Task from "../task-list/task";

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.store = mobxStore.taskStore;
    this.state = {
      tabIndex: 0,
    };

    this.onTabeChange = this.onTabeChange.bind(this);
  }

  onTabeChange(event) {
    this.setState((prevState) => ({
      ...prevState,
      tabIndex: event.activeIndex,
    }));
  }

  panes = [
    {
      menuItem: "Task List",
      render: () => (
        <Tab.Pane>
          <TaskList />
        </Tab.Pane>
      ),
    },
    {
      menuItem: "Add Task",
      render: () => (
        <Tab.Pane>
          <Task />
        </Tab.Pane>
      ),
    },
  ];

  render() {
    return (
      <>
        <Segment.Group>
          <Segment inverted color="blue">
            Task Manager
          </Segment>
          <Segment>
            <Tab
              activeIndex={this.state.tabIndex}
              panes={this.panes}
              onTabChange={this.onTabeChange}
            />
          </Segment>
        </Segment.Group>
      </>
    );
  }
}

export default observer(Home);
