import { action, makeObservable, observable } from "mobx";
import { apiFetchData } from "../common/api-fetch";

const task = {
  taskID: 0,
  description: "",
  status: false,
};

const search = {
  desc: "",
  status: "",
};

class TaskStore {
  newTask = { taskID: 0, description: "", status: false };
  tasks = [];
  search = { ...search };
  currentPage = 0;

  constructor() {
    //makeAutoObservable(this);
    makeObservable(this, {
      newTask: observable,
      tasks: observable,
      currentPage: observable,
      search: observable,
      changeNewTask: action,
      addTask: action,
      setTasks: action,
      updateTaskStatus: action,
      getAllTasks: action,
    });

    this.searchTasks = this.searchTasks.bind(this);
    this.addTask = this.addTask.bind(this);
    this.updateTaskStatus = this.updateTaskStatus.bind(this);
    this.getAllTasks = this.getAllTasks.bind(this);
    this.searchTasks = this.searchTasks.bind(this);
    this.changeNewTask = this.changeNewTask.bind(this);
    this.setTasks = this.setTasks.bind(this);
  }

  getAllTasks() {
    apiFetchData("api/tasks", "GET").then((data) => {
      this.setTasks(data);
    });
  }

  searchTasks() {
    apiFetchData(
      `api/tasks/search?query?desc=${this.search.desc};status=${this.search.status};`,
      "GET"
    ).then((res) => {
      this.tasks = res;
    });
  }

  changeNewTask(data) {
    let value = data.checked ? data.checked : data.value;
    this.newTask[data.id] = value;
  }

  setTasks(tasks) {
    this.tasks = [...tasks];
  }

  addTask() {
    if (!this.newTask.description) {
      alert("Cannot add empty task!");
      return;
    }

    //1. call save task API
    apiFetchData("api/tasks/", "POST", this.newTask).then(() => {
      this.newTask = { ...task };
    });
  }

  async updateTaskStatus(task) {
    task.status = !task.status;
    let newTask = { ...task };
    await apiFetchData("api/tasks/update", "POST", newTask);
  }
}

export default TaskStore;
