import { observer } from "mobx-react";
import React from "react";
import mobxStore from "../../mobx-store/mobex-store";
import { Table } from "semantic-ui-react";

class TaskList extends React.Component {
  constructor(props) {
    super(props);
    this.store = mobxStore.taskStore;
  }

  async componentDidMount() {
    await this.store.getAllTasks();
  }

  render() {
    return (
      <>
        {this.store.tasks.length === 0 && (
          <h2 style={{ textAlign: "center" }}>No data</h2>
        )}
        {this.store.tasks.length > 0 && (
          <>
            {/* <Grid>
              <Grid.Row>
                <Form>
                  <Form.Input></Form.Input>
                </Form>
              </Grid.Row>
            </Grid> */}
            <Table celled>
              <Table.Header>
                <Table.Row>
                  <Table.HeaderCell>Task ID</Table.HeaderCell>
                  <Table.HeaderCell>Description</Table.HeaderCell>
                  <Table.HeaderCell>Status</Table.HeaderCell>
                </Table.Row>
              </Table.Header>

              <Table.Body>
                {this.store.tasks.map((task, index) => {
                  return (
                    <Table.Row key={index}>
                      <Table.Cell>{task.taskID}</Table.Cell>
                      <Table.Cell>{task.description}</Table.Cell>
                      <Table.Cell>
                        <input
                          type="checkbox"
                          data-taskid={task.taskID}
                          checked={task.status}
                          onChange={(event) => {
                            this.store.updateTaskStatus(task);
                          }}
                        />
                      </Table.Cell>
                    </Table.Row>
                  );
                })}
              </Table.Body>
            </Table>
          </>
        )}
      </>
    );
  }
}

export default observer(TaskList);
